import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpModule } from '@angular/http';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { DescPage } from '../pages/desc/desc';

import { AboutUsPage } from '../pages/about-us/about-us';

import { DatePipe } from '@angular/common';

//Firebase
import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';

// Initialize Firebase
var config = {
  apiKey: "AIzaSyBv2wcWFSi7lgHDcrk3eZjgo2JjzNg4ytE",
  authDomain: "jobalerts-c7c62.firebaseapp.com",
  databaseURL: "https://jobalerts-c7c62.firebaseio.com",
  projectId: "jobalerts-c7c62",
  storageBucket: "jobalerts-c7c62.appspot.com",
  messagingSenderId: "308776394994"
};

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    DescPage,
    AboutUsPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp),

    AngularFireModule.initializeApp(config),
    AngularFirestoreModule.enablePersistence()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    DescPage,
    AboutUsPage
  ],
  providers: [
    DatePipe,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



